import { useState, useRef, useEffect, createRef, useCallback, useLayoutEffect } from 'react';
import './styles/base.scss';
import { ReactComponent as Avatar } from './assets/avatar.svg';
// import { ReactComponent as LogoBg } from './assets/logo-bg.svg';
// import { ReactComponent as NavBg } from './assets/nav-menu-bg.svg';
import { ReactComponent as Mobile } from './assets/mobile.svg';
import { ReactComponent as Laptop } from './assets/laptop.svg';
import { ReactComponent as Paint } from './assets/paint-brush.svg';
import { ReactComponent as WebSection } from './assets/web-section.svg';
import { ReactComponent as MobileSection } from './assets/mobile-section.svg';
import { ReactComponent as PaintSection } from './assets/paint-section.svg';
import { ReactComponent as ProjectImg } from './assets/projects.svg';
import { ReactComponent as Scroll } from './assets/scroll.svg';
import HabtOne from './assets/images/habts/one.png';
import HabtTwo from './assets/images/habts/two.png';
import HabtThree from './assets/images/habts/three.png';
import HabtFour from './assets/images/habts/four.png';
import LetsLinkOne from './assets/images/letslink/one.png';
import LetsLinkTwo from './assets/images/letslink/two.png';
import LetsLinkThree from './assets/images/letslink/three.png';
import LetsLinkFour from './assets/images/letslink/four.png';
import BooksAndBrewskies from './assets/images/books-and-brewskies.jpeg';

const data = [
    'header',
    'expertise-sec-one',
    'expertise-sec-two',
    'expertise-sec-three',
    'expertise-sec-four',
    'projects-sec-one',
    'projects-sec-two',
    'projects-sec-three',
    'projects-sec-four',
    'contact'
]

var keys = { 37: 1, 38: 1, 39: 1, 40: 1 };


const App = () => {
    const [name, setName] = useState();
    const [email, setEmail] = useState();
    const [message, setMessage] = useState();
    const [loading, setLoading] = useState(false);
    const [currentElement, setCurrentElement] = useState(0);
    const clicked = useRef()
    const elementsRef = useRef(data.map(() => createRef()));
    const timer = useRef();
    const mount = useRef();
    const touchStartPos = useRef();
    const touchEndPos = useRef();

    const preventDefault = useCallback((e) => {
        if (timer.current !== null) {
            clearTimeout(timer.current);
        }

        timer.current = setTimeout(() => {

            let val = 0;

            let elementLen = elementsRef.current.length;

            setCurrentElement((cPos) => {
                let nextNum = cPos;

                if (clicked.current) {
                    val = elementsRef.current[nextNum].current.offsetTop;
                } else {
                    if (e.deltaY > 0) {
                        if (nextNum < elementLen - 1) nextNum++
                    } else {
                        if (nextNum > 0) nextNum--
                    }

                    val = elementsRef.current[nextNum].current.offsetTop;
                }

                return nextNum;
            })

            clicked.current = false;

            window.scrollTo({
                top: val,
                behavior: 'smooth'
            });

        }, 100);

        e.preventDefault();
    }, []);

    const touchEnd = () => {
        let diff = touchStartPos.current - touchEndPos.current;

        if (diff > 100) {
            preventDefault({ deltaY: 1, preventDefault: () => undefined });
            return;
        }

        if (diff < -100) {
            preventDefault({ deltaY: -1, preventDefault: () => undefined });
            return;
        }
    }

    const touchMove = (e) => {
        touchEndPos.current = e.touches[0].clientY;
        e.preventDefault();
    }

    const touchStart = (e) => {
        touchStartPos.current = e.touches[0].clientY;
    }

    // call this to Disable
    const disableScroll = useCallback(() => {

        function preventDefaultForScrollKeys(e) {
            if (keys[e.keyCode]) {
                preventDefault(e);
                return false;
            }
        }

        var supportsPassive = false;
        try {
            window.addEventListener("test", null, Object.defineProperty({}, 'passive', {
                get: function () { supportsPassive = true; }
            }));
        } catch (e) { console.log(e) }

        var wheelOpt = supportsPassive ? { passive: false } : false;
        var wheelEvent = 'onwheel' in document.createElement('div') ? 'wheel' : 'mousewheel';

        window.addEventListener('DOMMouseScroll', preventDefault, false); // older FF
        window.addEventListener(wheelEvent, preventDefault, wheelOpt); // modern desktop
        window.addEventListener('keydown', preventDefaultForScrollKeys, false);

        window.addEventListener('touchmove', touchMove, wheelOpt); // mobile
        window.addEventListener('touchstart', touchStart); // mobile
        window.addEventListener('touchend', touchEnd); // mobile

    }, [])

    useLayoutEffect(() => {
        setTimeout(() => {
            window.scrollTo(0, 0);
        }, 500)
        setCurrentElement(0)
    }, [])


    useEffect(() => {
        mount.current = true;
        disableScroll()
    }, [disableScroll])

    const sendEmail = () => {
        if (!name || !message || !email) return alert('All fields required')
        setLoading(true)

        fetch("https://formsubmit.co/ajax/6fb9b6db755f974fe6a7d77b04c47d75", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({
                name: name,
                message: message,
                email: email
            })
        })
            .then(response => response.json())
            .then(data => {
                console.log(data)
                setLoading(false)
                alert("Thank you for reaching out! I will respond as soon as I can.")
            })
            .catch(error => {
                console.log(error)
                alert('Sorry, something went wrong trying to send your request. Please try again.')
                setLoading(false)
            });
    }

    const onMenuItemClick = (name) => {
        let val = 0;
        switch (name) {
            case 'expertise':
                val = elementsRef.current[1].current.offsetTop;
                setCurrentElement(1);
                break;
            case 'projects':
                val = elementsRef.current[5].current.offsetTop;
                setCurrentElement(5)
                break;
            case 'contact':
                val = elementsRef.current[9].current.offsetTop;
                setCurrentElement(9)
                break
            default:
                val = 0
                setCurrentElement(0);
        }

        clicked.current = true;

        return window.scrollTo({
            top: val,
            behavior: 'smooth'
        });
    }

    const isPosAtElement = (index) => {
        if (index === currentElement) return 'slideBottomToUp';
        return '';
    }

    return (
        <div className="container">
            {/* <LogoBg id='logo-bg' />
            <NavBg id='nav-bg' /> */}
            <nav className={`navbar ${currentElement > 0 ? 'nav-bg-primary' : mount.current ? 'nav-bg-transparent' : ''}`}>
                <div className="navbar__content">
                    <div className="col col-left">
                        <div className='logo'>
                            <div className="chevron chevron-logo chevron-initial" />
                            <div className="chevron chevron-logo chevron-flipped" />
                        </div>
                        <span onClick={() => onMenuItemClick('top')}>
                            Portfolio
                    </span>
                    </div>
                    <div className="col col-right">
                        <button onClick={() => onMenuItemClick('expertise')} className={currentElement > 0 && currentElement < 5 && 'hover'}>expertise</button>
                        <button onClick={() => onMenuItemClick('projects')} className={currentElement > 4 && currentElement < 9 && 'hover'}>projects</button>
                        <button onClick={() => onMenuItemClick('contact')} className={currentElement > 8 && 'hover'}>contact</button>
                    </div>
                </div>
            </nav>
            <section className="header" ref={elementsRef.current[0]}>
                <div className="header__content">
                    <div className="header__text">
                        <h1>John Nguyen</h1>
                        <h2>Hi! I'm a developer and welcome to my portfolio</h2>
                    </div>
                    <div className="header__avatar">
                        <Avatar />
                    </div>
                    <div className="scroll">
                        <strong>scroll</strong>
                        <Scroll id='scroll-shape' />
                    </div>
                </div>
            </section>
            <section className='body'>
                <section className="expertise">

                    <section className={`expertise__section-1 ${isPosAtElement(1)}`} ref={elementsRef.current[1]}>
                        <Paint id='expertise__section-1__paint' className='expertise__section-1__svg' />
                        <Laptop id='expertise__section-1__laptop' className='expertise__section-1__svg' />
                        <Mobile id='expertise__section-1__mobile' className='expertise__section-1__svg' />
                        <h1>My Expertise</h1>
                    </section>

                    <section className={`expertise__section-2 ${isPosAtElement(2)}`} ref={elementsRef.current[2]}>
                        <div className="expertise__section__content">
                            <Laptop id='expertise__section-2__laptop' />
                            <h1>Website Development</h1>
                            <p>
                                I consider <u>JavaScript, HTML, and CSS</u> to be my specialty, but I also have experience with Python, Java, SQL, and even Visual Basic.
                            </p>
                            <WebSection id='expertise__section-2__laptop-svg' />
                        </div>
                    </section>


                    <section className={`expertise__section-3 ${isPosAtElement(3)}`} ref={elementsRef.current[3]}>
                        <div className="expertise__section__content">
                            <Mobile id='expertise__section-3__mobile' />
                            <h1>Mobile Development</h1>
                            <p>I specialize in React and have experience building mobile and web applications with React.</p>
                            <MobileSection id='expertise__section-3__mobile-svg' />
                        </div>
                    </section>

                    <section className={`expertise__section-4 ${isPosAtElement(4)}`} ref={elementsRef.current[4]}>
                        <div className="expertise__section__content">
                            <Paint id='expertise__section-4__paint' />
                            <h1>Design</h1>
                            <p>
                                UI/UX design isn't my specialty, but I enjoy learning and improving my skills in both design and development.
                    </p>
                            <PaintSection id='expertise__section-4__paint-svg' />
                        </div>
                    </section>

                </section>
                <section className="projects">

                    <section className={`projects__section-1 ${isPosAtElement(5)}`} ref={elementsRef.current[5]}>
                        <div className="col">
                            <h1>Projects</h1>
                        </div>
                        <div className="col">
                            <ProjectImg id='projects-svg' />
                        </div>
                    </section>

                    <section className={`projects__section-2 ${isPosAtElement(6)}`} ref={elementsRef.current[6]}>
                        <h1>Habts</h1>
                        <p>Is a React Native App. Main Functionlities include; create/update habits, notifications/reminders, reward system (badges), and reflection tool.</p>
                        <div className="projects__section-2__imgs">
                            <div className="col"> <img src={HabtOne} alt="habt-one" /></div>
                            <div className="col"><img src={HabtTwo} alt="habt-two" /></div>
                            <div className="col"><img src={HabtThree} alt="habt-three" /></div>
                            <div className="col"><img src={HabtFour} alt="habt-four" /></div>
                        </div>
                    </section>

                    <section className={`projects__section-3 ${isPosAtElement(7)}`} ref={elementsRef.current[7]}>
                        <h1>LetsLink</h1>
                        <p>Is a React Native App. Main functionalities include; algorithm to show users within a certain radius, create/update request, profile page including multiple image upload and caching mechanisms, and messaging.</p>
                        <div className="projects__section-3__imgs">
                            <div className="col"> <img src={LetsLinkOne} alt="link-one" /></div>
                            <div className="col"><img src={LetsLinkTwo} alt="link-two" /></div>
                            <div className="col"><img src={LetsLinkThree} alt="link-three" /></div>
                            <div className="col"><img src={LetsLinkFour} alt="link-four" /></div>
                        </div>
                    </section>

                    <section className={`projects__section-4 ${isPosAtElement(8)}`} ref={elementsRef.current[8]}>
                        <h1>Books and Brewskies</h1>
                        <p>Is a ecommerce website. Main functionalities include; accepting payments by interacting with stripes API, dynamic cart, restricted admin page, and blog.</p>
                        <div className="projects__section-4__imgs">
                            <div className="col">
                                <img src={BooksAndBrewskies} alt="books" />
                            </div>
                        </div>
                    </section>
                </section>

                <section className={"contact " + isPosAtElement(9)} ref={elementsRef.current[9]}>
                    <div className='form'>
                        <h1>Contact Me!</h1>
                        <p>If you have any <u>project ideas</u>, want to <u>work with me</u>, or just have anything to say, fill out your <u>contact information</u> and a <u>brief message below</u>. I look forward to speaking with you.</p>

                        <label>
                            Name:
                                <input type='text' onChange={(e) => setName(e.target.value)} maxLength={100} placeholder='John Nguyen' />
                        </label>

                        <label>
                            Email:
                                <input type='text' onChange={(e) => setEmail(e.target.value)} maxLength={100} placeholder='john.doe@gmail.com' />
                        </label>
                        <label>
                            Message:
                                <textarea
                                type='text'
                                onChange={(e) => setMessage(e.target.value)}
                                maxLength={500}
                                placeholder="What's on your mind?"
                                rows="3"
                            />
                        </label>
                        <div className='submit'>
                            <button onClick={sendEmail}>{loading ? 'Sending..' : 'Send'}</button>
                        </div>
                    </div>
                </section>
                <button className='backToTop' onClick={() => onMenuItemClick('')}>
                    <p>Top</p>
                    <div className="chevron" />
                </button>
            </section>
        </div>
    )
}

export default App;